#meter {
    width: 300px;
    height: 15px;
    /* border: 1px solid black; */
    position: absolute;
    top: 30px; /* Adjust the distance from the bottom */
    /* transform: translateX(50%); change if #game-canvas align-items: is changed*/ 
    overflow: hidden;
    z-index: 2;
    display: none;
}

.progress {
    height:100%;
    width:100%;
    /* border:1px solid #000; */
    background: linear-gradient(to right, #DD2C00 35%, #66FF99 45%, #66FF99 55%, #DD2C00 65%);
}

.circle {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: white;
    border: 1px solid black;
    position: absolute;
    top: 0;
    left: 0;
}

.timer-bar {
    width: 100%;
    height: 8px;
    background-color: #f2f2f2;
}
  
.status-bar {
    height: 100%;
    background-color: #4caf50;
    width: 100%;
    transition: width 0.1s linear; /* Adjust the transition speed as needed */
}
  
  
